<template>
  <div class="box" v-show="receiveShow">
    <div class="main" v-show="mainShow">
      <img class="imgT" src="http://fkzzz.oss-cn-qingdao.aliyuncs.com/img/gc_21.png"/>
      <p @click="goto">立即领取</p>
      <img @click="cancelbtn" class="imgB" src="../../assets/images/room/gc_23.png"/>
    </div>
    <div class="mainback" v-show="mainbackShow">
      <img class="imgT" src="http://fkzzz.oss-cn-qingdao.aliyuncs.com/img/gc_22.png"/>
      <div class="receiveCont">
        <a class="qian"><span>+{{num}}</span> 钻石</a>
        <span class="mas">马上去抓娃娃吧!</span>
      </div>
      <p @click="cancelbtn">已领取</p>
      <img class="imgB" @click="cancelbtn" src="../../assets/images/room/gc_23.png"/>
    </div>
  </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        mainShow: true,
        mainbackShow: false,
        receiveShow: true,
        num: ""
      }
    },
    components: {},
    mounted: function () {
      var _this = this;
      var getdiamond = getCookie("getdiamond");
      var port = getCookie("port");
      if (port === "true" && getdiamond === "false") {
        _this.receiveShow = true;
      } else {
        _this.receiveShow = false;
      }
    },
    methods: {
      cancelbtn: function () {
        this.receiveShow = false;
      },
      goto: function () {
        var _this = this;
        _this.mainShow = false;
        _this.mainbackShow = true;
        setCookie("getdiamond", true);
        var userid = getCookie("userId");
        var token = getCookie("wawajitoken");
        $.ajax({
          type: 'GET',
          url: urlhost + "usercenter/getdiamond",
          data$Type: "jsonp",
          data: {
            "user_id": userid,
            "token": token,
          },
          success: function (response) {
            var mess = JSON.parse(response);
            if (mess.status == 1) {
              _this.num = mess.message
            }
          },
        });
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  }

  ;
  .box {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    .main {
      width: n(465);
      height: n(600);
      position: relative;
      .imgT {
        width: n(465);
        height: n(485);
      }
      .imgB {
        position: absolute;
        top: n(550);
        left: n(207.5);
        width: n(50);
        height: n(50);
      }
      p {
        width: 60%;
        text-align: center;
        line-height: n(50);
        margin-left: 20%;
        height: n(50);
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        background: #ffcf13;
        position: absolute;
        top: 60%;
      }
    }
    .mainback {
      width: n(465);
      height: n(600);
      position: relative;
      .receiveCont {
        position: absolute;
        top: n(50);
        left: 0;
        width: 100%;
        height: n(200);
        display: inline-flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .qian {
          margin-bottom: 5px;
          span {
            font-size: n(38);
            margin-right: 5px;
          }
        }
      }
      .imgT {
        width: n(465);
        height: n(485);
      }
      .imgB {
        position: absolute;
        top: n(550);
        left: n(207.5);
        width: n(50);
        height: n(50);
      }
      p {
        width: 60%;
        text-align: center;
        line-height: n(50);
        margin-left: 20%;
        height: n(50);
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        background: #ffcf13;
        position: absolute;
        top: 60%;
      }
    }
  }
</style>




