<template>
    <div class="noticeBody" v-show="noticeShow">
      <div class="main">
        <ul id="noticeList">
          <li v-for="item in noticeCont">{{item.content}}</li>

        </ul>
        <img @click="show" src="../assets/images/Login/gc_16.png"/>
      </div>
    </div>
</template>
<script>
  var urlhost = process.env.API_AJAX;
  export default {
    data() {
      return {
        noticeShow:false,
        noticeCont:[]
      }
    },
    components: {

    },
    mounted:function () {
      var _this = this;
      var num = 1;

        $.ajax({
          type: 'GET',
          url: urlhost+"notice",
          data$Type: "jsonp",
          data: {
          },
          success: function (response) {
            var mess = JSON.parse(response).message;
            if(mess.length < 1){
              _this.noticeShow = false;
            }else {
              _this.noticeShow = true;
              _this.noticeCont = mess;
              var list = document.getElementById('noticeList');
              var aLi = document.getElementsByTagName('li');
              var num = 0;
              var n = list.getElementsByTagName('li').length;
              var newLeft = parseInt(list.offsetLeft);
              var speed = 1;
              var w;
              var s = 0;

              for (var i = 0; i < aLi.length; i++) {
                w = parseInt(aLi[i].offsetWidth) - 0;
                s += w;
              }
              var total = s + n * 600;

              var timer = setInterval(function () {
                newLeft = 600 - speed * num;
                num++;
                if (num >= total) {
                  num = 0;
                  newLeft = 600 - speed * num;
                }
                list.style.left = newLeft + 'px';
              }, 10)
            }
          },
        });
    },
    methods: {
      show:function () {
        this.noticeShow = false;
      }
    }
  }
</script>
<style lang="scss" scoped>
  @function n($n) {
  @return $n  / 64 + rem;
  };
  .noticeBody{
    position: fixed;
    top:n(80);
    left:0;
    width: 100%;
    height: n(50);
    background: rgba(0, 0, 0, 0.3);
    z-index: 99999;
    display: inline-flex;
    align-items: center;
   .main{
     position: relative;
     width: 100%;
     height: 100%;
     /*display: inline-flex;*/
     /*align-items: center;*/
     ul{
       position: absolute;
       width: 20000px;
       height: n(50);
       li{
         float: left;
         line-height: n(50);
         color: red;
         margin-right: 600px;
         /*margin-left: 100%;*/
       }
     }

     img{
       position: absolute;
       top:n(10);
       right: 0;
       width: (30);
       height: n(30);
       -webkit-border-radius: 50%;
       -moz-border-radius: 50%;
       border-radius: 50%;
     }
   }
  }


</style>




