        var _this = this;
        var wsServer =  process.env.API_HOST;
        _this.cxt1 = _this.$refs.cvs1;
        _this.cxt2 = _this.$refs.cvs2;
        _this.websocket = new WebSocket(wsServer);
        _this.websocket.onopen = function (evt) { _this.onOpen(evt) };
        _this.websocket.onclose = function (evt) { _this.onClose(evt) };
        _this.websocket.onmessage = function (evt) { _this.onMessage(evt) };
        _this.websocket.onerror = function (evt) { _this.onError(evt) };

      },
      methods: {
        bolt:function () {
          var _this = this;
          var token = getCookie('wawajitoken');
          if (token) {
            var repeatline = {"type": 20000, "mark": _this.mark, "token": token, "room_id": _this.roomidtwo};
            var repeatlinestr = JSON.stringify(repeatline);
            if (_this.websocket.readyState == 1) {
              _this.websocket.send(repeatlinestr)
            } else{
              setTimeout(function () {
                _this.websocket.send(tokenString)
              }, 300);
            }
          }else{
            var repeatline = {"type":20000,"mark":_this.mark,"token":_this.youketoken,"room_id":_this.roomidtwo};
            var repeatlinestr = JSON.stringify(repeatline);
            if (_this.websocket.readyState == 1) {
