                    if( cancelNum.operation_member.length ==0 && cancelNum.queue_count ==0 ){
                        _this.according = true;
                      _this.startShow = true;
                      _this.appointBtnShow = false;
                      _this.cancellationShow = false;
                    }else if( cancelNum.operation_member.length ==0 && cancelNum.queue_count !=0){
                      if(_this.cancellationShow == true){
                        _this.startShow = false;
                        _this.appointBtnShow = true;
                        _this.cancellationShow = false;
                        if(_this.queue_count > 1){
                          _this.queue_count = _this.queue_count - 1;
                        }
                      }else{
                        _this.startShow = false;
                        _this.appointBtnShow = true;
                        _this.cancellationShow = false;
                      }
                    }else{
                        _this.according = false;
                      if(_this.userId == cancelNum.user_id){
                        if(_this.cancellationShow == true){
                          _this.startShow = false;
                          _this.appointBtnShow = false;
                          _this.cancellationShow = true;
                        }else{
                          _this.startShow = false;
                          _this.appointBtnShow = true;
                          _this.cancellationShow = false;
                        }

                      }
                    }
                  }
                  break;
                case 20062:
//
                  break;

                case 20031:
                  var dollDetails = JSON.parse(evt.data).body;
                  var dollObj = JSON.parse(dollDetails);
                  _this.imgname = dollObj;
                  _this.imgUrl = JSON.parse(dollObj.imgs_url);
                  break;
                case 10022://放回首页房间列表信息
                  _this.loading = false;//停止加载中
                  var dollJson = JSON.parse(evt.data);
                  var dollBody = dollJson.body;
                  _this.dollList = JSON.parse(dollBody);


                  break;
                default:
              }
            }
