        touchMoveGame:function (event) {
          var _this = this;
          event.preventDefault();
          var box = document.getElementById('box');
          var touchDiv = document.getElementById('touchDiv');
          var touch = event.targetTouches[0];
          var x = (touch.pageX-box.offsetLeft) - touchDiv.offsetWidth/2;
          var y = (touch.pageY-box.offsetTop) - touchDiv.offsetHeight/2;

          var l_dir=-1;

          if( x < 0 ){
            x=0;
          }else if(x>box.offsetWidth-touchDiv.offsetWidth){
            x=box.offsetWidth-touchDiv.offsetWidth;
          }

          if( y < 0 ){
            y=0;

          }else if(y>box.offsetHeight-touchDiv.offsetHeight){
            y=box.offsetHeight-touchDiv.offsetHeight;
          }
          var jx =  (touch.pageX-box.offsetLeft) - box.offsetWidth/2;
          var jy = (touch.pageY-box.offsetTop) - box.offsetHeight/2;
          touchDiv.style.left = x+'px';
          touchDiv.style.top = y+'px';
          var cx = 0;
          var cy = 0;
          var tanx = Math.abs(jx-cx);
          var tany = Math.abs(jy-cy);
          var z = Math.sqrt(Math.pow(tanx,2)+Math.pow(tany,2));
          var cos = tany/z;
          var radina = Math.acos(cos);
          var angle = Math.floor(180/(Math.PI/radina));
          if(jx>0&&angle<90&&angle>67.5){
            l_dir=0;
            if(g_dir !=l_dir)
            {
              g_dir = l_dir;
              if(_this.canvasShow2 == false){
                _this.controlJson.cmd = "move,1,0";
                var userToken = getCookie('wawajitoken');
                _this.controlJson.room_id = _this.roomId;
                _this.controlJson.token = userToken;
                var str_right1 = JSON.stringify(_this.controlJson);
                _this.websocket.send(str_right1);
              }else{
                _this.controlJson.cmd = "move,0,1";
                var userToken = getCookie('wawajitoken');
                _this.controlJson.room_id = _this.roomId;
                _this.controlJson.token = userToken;
                var str_right1 = JSON.stringify(_this.controlJson);
                _this.websocket.send(str_right1);
              }
            }

          }else if(jx>0&&jy>0&&angle>22.5&&angle<67.5){
            l_dir=7;
            if(g_dir !=l_dir)
            {
              g_dir = l_dir;
              if(_this.canvasShow2 == false){
                _this.controlJson.cmd = "move,1,-1";
                var userToken = getCookie('wawajitoken');
                _this.controlJson.room_id = _this.roomId;
                _this.controlJson.token = userToken;
                var str_rightB = JSON.stringify(_this.controlJson);
                _this.websocket.send(str_rightB);
              }else{
                _this.controlJson.cmd = "move,1,1";
                var userToken = getCookie('wawajitoken');
                _this.controlJson.room_id = _this.roomId;
                _this.controlJson.token = userToken;
                var str_rightB = JSON.stringify(_this.controlJson);
                _this.websocket.send(str_rightB);
              }
            }


          }else if(angle<22.5&&angle>0&&jy>0){
            l_dir=6;
            if(g_dir !=l_dir)
            {
              g_dir = l_dir;
              if(_this.canvasShow2 == false){
