            background-size: 100% 100%;
      }
      span:nth-child(2){
        left: 0%;
        background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
      }
      span:nth-child(3){
        left: 20%;
        background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
        -webkit-animation:circleLoad_1 1.5s ease-in;
