        background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
        -webkit-animation:circleLoad_5 7.5s ease-in;
      }
      @-webkit-keyframes circleLoad_1{
            0%,66.66%{
                background:url("../assets/images/room/game_21.png") no-repeat;
            background-size: 100% 100%;
            }
            100%{
                background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
            }
        }
        @-webkit-keyframes circleLoad_2{
            0%,83.34%{
                 background:url("../assets/images/room/game_21.png") no-repeat;
            background-size: 100% 100%;
            }
            100%{
                background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
            }
        }
        @-webkit-keyframes circleLoad_3{
            0%,88.88%{
