            background-size: 100% 100%;
            }
            100%{
               background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
            }
        }
        @-webkit-keyframes circleLoad_4{
            0%,91.67%{
                background:url("../assets/images/room/game_21.png") no-repeat;
            background-size: 100% 100%;
            }
            100%{
               background:url("../assets/images/room/game_19.png") no-repeat;
        background-size: 100% 100%;
            }
        }
      }
